.mapouter {
  position: relative;
  width: 100%;
  overflow: hidden;
  padding-top: 75%;
  max-height:500px;
  max-width:600px;
}

.gmap-canvas {
  position: relative;
  width: 100%;
  padding-top: 75%;
}

.gmap_iframe {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

@media (min-width: 600px) {
  .gmap_iframe {
      display: flex;
  }
}
